@import "../../assets/fonts.scss";

.header {
  display: flex;
  justify-content: flex-start;
  flex-direction: column;
  background-color: #fafbfb;
  padding: 40px 172px 0 172px;

  @media (max-width: 1366px) {
    padding: 40px 110px 0 110px;
  }
  .title {
    display: flex;
    flex-direction: column;

    &__header {
      margin: 0 0 10px 0;
    }

    &__count {
      margin: 0;
      opacity: 0.5;
    }
  }

  .input_wrapper {
    display: inline-block;
    position: relative;
    height: 46px;
    padding: 1px;

    .search {
      height: 46px;
      width: 446px;
      padding-left: 70px;
      font-size: 18px;
    }

    &:before {
      display: inline-block;
      position: absolute;
      top: 14px;
      left: 17px;
      padding: 0 16px 0 0;
      content: "";
      height: 19px;
      width: 20px;
      border-right: 1px solid #d4d9dc;
      background: url("../../../public/img/search.svg") no-repeat;
    }
  }

  &__wrapper {
    display: flex;
    align-items: center;

    > *:not(:last-child) {
      margin-right: 40px;
    }

    margin: 19.5px 0;

    .sort {
      height: 45px;
      width: 45px;
      border: 1px solid #d4d9dc;
      border-radius: 2px;
      cursor: pointer;
      background: url("../../../public/img/sort.svg") no-repeat;
      background-position: 10px;
    }

    .label {
      font-family: "Proxima Nova Rg";
      font-size: 18px;
      line-height: 20px;
    }

    .customCheckbox {
      position: absolute;
      opacity: 0;
      cursor: pointer;
      margin: 4px;

      & + label {
        display: inline-flex;
        align-items: center;
        user-select: none;

        &:before {
          content: "";
          display: inline-block;
          width: 20px;
          height: 20px;
          border: 1px solid #adb5bd;
          border-radius: 0.25em;
          margin-right: 13px;
          background-repeat: no-repeat;
          background-position: center center;
          background-size: 11px 8px;
          cursor: pointer;
        }
      }

      &:checked + label:before {
        border-color: #208cff;
        background-color: #208cff;
        background-image: url("../../../public/img/checked.svg");
      }
    }

    .customCheckbox:not(:disabled):not(:checked) + label:hover::before {
      border-color: #b3d7ff;
    }

    .customCheckbox:not(:disabled):active + label::before {
      background-color: #208cff;
      border-color: #208cff;
    }

    .customCheckbox:disabled + label::before {
      background-color: #208cff;
    }
  }
}

.content {
  padding: 0 130px;

  @media (max-width: 1366px) {
    padding: 0 110px;
  }
}
